import React from 'react'
import { useDispatch, useSelector } from "react-redux";
import { useTranslation } from "react-i18next";
// import {
// 	Space,
// } from 'antd';

import './Toolbar.css';

import {
	initializeAll,
	concatenate_generated_cfg,
	concatenate_generated_code,
	replacejscssfile,
	chipSeriesToChipType,
	decideNewChipType,
	decideNewPartNumberType,
	getGlobalVariables,
} from "../content-web-commonLib"

import peripheral from "../images/CodeGenerater/peripheral.png"; // Peripheral
import pin from "../images/CodeGenerater/pin.png"; // Pin
import clock from "../images/CodeGenerater/clock.png"; // Clock

import tree_view from "../images/PinConfig/tree_view.png"; // Switch
import save from "../images/PinConfig/save.png"; // Save Configuration
import load from "../images/PinConfig/load.png" // Load Configuration
import gen_code from "../images/PinConfig/gen_code.png"; // Generate Code

import restore from "../images/CodeGenerater/restore.png"; // Restore

import switchClockTree from "../images/ClockConfig/switchClockTree.png" // Switch Clock Tree

import connect_chip_2 from "../images/PinConfig/connect_chip_2.png"; // Connect to Target Chip
import print_report from "../images/PinConfig/print_report.png"; // Print Report
import gen_pin_description_report from "../images/PinConfig/gen_pin_description_report.png"; // Generate Report
import switch_pin_description from "../images/PinConfig/switch_pin_description.png"; // Switch Pin Description

import disable from "../images/PinConfig/disable.png"; // Disable

import settings from "../images/PinConfig/settings_3.png"; // Settings
import user_manual from "../images/PinConfig/user_manual.png"; //Read User Manual

import {
	DEFAULT_VALUE,
	CURRENT_TOOL,
	RESTRICTION_FOLDER_NAME,
} from '../content-web-constants';

import { setChipType } from "../store/slices/r_chipType";
import { set_partNumber_package } from "../store/slices/r_partNumber_package";
import { set_partNumber_type } from "../store/slices/r_partNumber_type";
import { setSnippetName } from "../store/slices/r_snippetName";
import { set_cfg_perFunctions } from "../store/slices/r_cfg_perFunctions";
import { setCurrentTool} from "../store/slices/r_currentTool";

const { CHIP_TYPE, PART_NUMBER } = DEFAULT_VALUE;

// [Workaround] Force copy some functions in NUC_Peripheral_Configuration.js
NUTOOL_PER.concatenate_generated_cfg = concatenate_generated_cfg;
NUTOOL_PER.concatenate_generated_code = concatenate_generated_code;

try {
	NUTOOL_PIN.switchToPinConfig();
} catch ( e ) {
	console.error(e);
}

const Toolbar = (props) => {
	const { i18n } = useTranslation();
	const r_chipType = useSelector((state) => state.r_chipType);
	const r_partNumber_package = useSelector((state) => state.r_partNumber_package);
	// const r_cfg_perFunctions = useSelector((state) => state.r_cfg_perFunctions);
	const r_currentTool = useSelector((state) => state.r_currentTool)
	const dispatch = useDispatch();

	// // From fterMCUchange()
	// const handleChangePartNo = (value, node) => {
	// 	dispatch(set_partNumber_package(value));
	// 	dispatch(set_partNumber_type(node.type));
	// 	dispatch(set_cfg_perFunctions([]));
	// 	dispatch(setSnippetName(""));
	// };

	const bHidden = function(array=[]){
		return array.includes(r_currentTool);
	}

	$('#loadConfiguration').off('change').on('change', function(){
		if (this.files[0] === undefined || this.files[0] == null) return;
			var extension = this.files[0].name.split('.').pop().toLowerCase();  // file extension from input file
			if (extension.indexOf('cfg') === -1) {
				alert("Please select a file with a valid file type. (.ncfg)")
				return;
			}
			var uploadedFile = this.files[0];
			var fileReader = new FileReader();
			fileReader.readAsText(uploadedFile)
			fileReader.onload = function (reader) {
				try {
					// NUTOOL_PIN.g_readConfigFileContentText = reader.target.result;
					// NUTOOL_PIN.g_readConfigFilePath = URL.createObjectURL(uploadedFile);
					var result = reader.target.result.replace(/\n/g, "\r\n"); // Unix(LF) -> Windows(CRLF);
					var g_readConfigFilePath = URL.createObjectURL(uploadedFile);
		
					var newPartNumberPackage = result.sliceAfterX('MCU:').slicePriorToX('\r');
					var newChipType = decideNewChipType(newPartNumberPackage);
					var newPartNumberType = decideNewPartNumberType(newPartNumberPackage);
					var oldfilename = 'NUC_' + r_chipType + '_Content.js';
					var newfilename = 'NUC_' + newChipType + '_Content.js';

					NUTOOL_PER.showPinConfigure();
					NUTOOL_PIN.g_readConfigFileContentText = result;
					NUTOOL_PIN.g_readConfigFilePath = g_readConfigFilePath;
					NUTOOL_PIN.replaceJsFile(NUTOOL_PIN.loadConfig_core);
	
					NUTOOL_PER.showClockConfigure();
					NUTOOL_CLOCK.g_readConfigFileContentText = result;
					NUTOOL_CLOCK.g_readConfigFilePath = g_readConfigFilePath;
					NUTOOL_CLOCK.loadConfig_core();

					dispatch(setCurrentTool(CURRENT_TOOL.PERIPHERAL));
					NUTOOL_PER.showPeripheralConfigure();
					replacejscssfile(oldfilename, newfilename, 'js', function(){
						initializeAll();
						NUTOOL_PER.readTreeContent(result);
						dispatch(setChipType(newChipType));
						dispatch(set_partNumber_package(newPartNumberPackage));
						dispatch(set_partNumber_type((newPartNumberType)));
						dispatch(set_cfg_perFunctions(
							getGlobalVariables("NUTOOL_PER", "g_enabledPeripheralFunctionNodes")
						));
						dispatch(setSnippetName(''));
					});
				}
				catch (err) {
					console.error(err);
				}
			}
	});

	$('#bspForGenCode').off('change').on('change', function(){
		// TODO - Lack confirm function
		var file = this.files[0];
		if(file && file.webkitRelativePath.startsWith(RESTRICTION_FOLDER_NAME)){
			var path = file.path.slicePriorToX(RESTRICTION_FOLDER_NAME) + RESTRICTION_FOLDER_NAME;
			$("#generateCodeDialogInput").val(path);
		} 
	});
	
	return (
		<div
			className="nu-toolbar"
		>
			<img
				src={peripheral}
				alt="Peripheral"
				onClick={() => {
					dispatch(setCurrentTool(CURRENT_TOOL.PERIPHERAL));
					NUTOOL_PER.showPeripheralConfigure();
				}}
			/>
			<img
				src={pin}
				alt="Pin"
				onClick={() => {
					dispatch(setCurrentTool(CURRENT_TOOL.PIN));
					NUTOOL_PIN.setg_selectedPartNoValue(r_partNumber_package);
					NUTOOL_PIN.loadPinConfigureTool_fromSelectionGuide();
					NUTOOL_PER.showPinConfigure();
					// NUTOOL_PIN.changeUIlanguage();
				}}
			/>
			<img
				src={clock}
				alt="Clock"
				onClick={() => {
					dispatch(setCurrentTool(CURRENT_TOOL.CLOCK));
					NUTOOL_CLOCK.setg_selectedPartNoValue(r_partNumber_package);
					NUTOOL_CLOCK.loadClockConfigureTool_fromSelectionGuide();
					NUTOOL_PER.showClockConfigure();
				}}
			/>
			<img
				id="ID_BUTTON_SHOW_REGISTERS"
				src={tree_view}
				alt="Switch"
				onClick={() => {
					switch(r_currentTool) {
						case CURRENT_TOOL.PERIPHERAL:
							let cmp = $('#CodeGeneratorContainer');
							if( cmp.css("position") === "absolute" ){
								cmp.css("position", "static");
							}
							else {
								cmp.css("position", "absolute");
							}
							break;
						case CURRENT_TOOL.PIN:
							NUTOOL_PIN.showRegisters();
							break;
						case CURRENT_TOOL.CLOCK:
							NUTOOL_CLOCK.showLeftPanel();
							break;
						default:
							break;
					}
				}}
			/>
			<img
				id="ID_BUTTON_LOAD"
				src={load}
				alt="Load"
				onClick={() => {
					$('#loadConfiguration').trigger('click');
				}}
			/>
			<img
				id="ID_BUTTON_SAVE"
				src={save}
				alt="Save"
				onClick={() => {
						try {
							// eslint-disable-next-line no-restricted-globals
							var text = external.generatePackage_ncfg();
							var blob = new Blob([text], { type: "text/plain;charset=utf-8" });
							saveAs(blob, `${r_partNumber_package}.ncfg`);
						} catch (err) {
							console.log("saveConfig failed, meg:" + err);
						}
					//}
				}}
			/>
			<img
				src={gen_code}
				alt="Generate"
				onClick={() => {
					switch(r_currentTool) {
						case CURRENT_TOOL.PERIPHERAL:
							// TODO
							NUTOOL_PER.generateCode();
							break;
						case CURRENT_TOOL.PIN:
						case CURRENT_TOOL.CLOCK:
							dispatch(setCurrentTool(CURRENT_TOOL.PERIPHERAL));
							NUTOOL_PER.showPeripheralConfigure();
							NUTOOL_PER.generateCode();
							break;
						default:
							break;
					}
				}}
			/>
			<img
				src={restore}
				hidden={bHidden([CURRENT_TOOL.PIN])}
				alt="Restore"
				onClick={() => {
					switch(r_currentTool) {
						case CURRENT_TOOL.PERIPHERAL:
							// NUTOOL_PER.restoreToDefault();
							var oldfilename = 'NUC_' + r_chipType + '_Content.js';
							var newfilename = 'NUC_' + chipSeriesToChipType(CHIP_TYPE) + '_Content.js';
							replacejscssfile(oldfilename, newfilename, 'js', function(){
								initializeAll();
								dispatch(setChipType(CHIP_TYPE));
								dispatch(set_partNumber_package(PART_NUMBER[CHIP_TYPE].package));
								dispatch(set_partNumber_type(PART_NUMBER[CHIP_TYPE].type));
								dispatch(set_cfg_perFunctions([]));
								dispatch(setSnippetName(''));
							});
							break;
						case CURRENT_TOOL.CLOCK:
							NUTOOL_CLOCK.pageRefresh();
							break;
						default:
							break;
					}
				}}
			/>
			<img
				src={switchClockTree}
				alt="Switch Clock Tree"
				hidden={bHidden([CURRENT_TOOL.PERIPHERAL, CURRENT_TOOL.PIN])}
				onClick={() => {
					NUTOOL_CLOCK.switchD3ClockTree();
				}}
			/>
			<img
				id="ID_BUTTON_CONNECT_TO_TARGET"
				src={connect_chip_2}
				alt="Connect"
				hidden={bHidden([CURRENT_TOOL.PERIPHERAL, CURRENT_TOOL.CLOCK])}
				onClick={() => {
					switch(r_currentTool) {
						case CURRENT_TOOL.PIN:
							NUTOOL_PIN.connectToChip();
							break;
						default:
							break;
					}
				}}
			/>
			<img
				id="ID_BUTTON_PRINT_REPORT"
				src={print_report}
				alt="Print Report"
				hidden={bHidden([CURRENT_TOOL.PERIPHERAL, CURRENT_TOOL.CLOCK])}
				onClick={() => {
					switch(r_currentTool) {
						case CURRENT_TOOL.PIN:
							NUTOOL_PIN.printReport();
							break;
						default:
							break;
					}
				}}
			/>

			<img
				id="ID_BUTTON_GENERATE_PIN_DESCRIPTIONS"
				src={gen_pin_description_report}
				hidden={bHidden([CURRENT_TOOL.PERIPHERAL, CURRENT_TOOL.CLOCK])}
				alt="Generate Report"
				onClick={() => {
					switch(r_currentTool) {
						case CURRENT_TOOL.PIN:
							NUTOOL_PIN.generatePinDescriptions();
							break;
						default:
							break;
					}
				}}
			/>
			<img
				id="ID_BUTTON_SHOW_PIN_DESCRIPTIONS"
				src={switch_pin_description}
				hidden={bHidden([CURRENT_TOOL.PERIPHERAL, CURRENT_TOOL.CLOCK])}
				alt="Switch Pin Descript"
				onClick={() => {
					switch(r_currentTool) {
						case CURRENT_TOOL.PIN:
							NUTOOL_PIN.showPinDescriptions();
							break;
						default:
							break;
					}
				}}
			/>
			<img
				src={disable}
				alt="Disable"
				hidden={bHidden([CURRENT_TOOL.PERIPHERAL])}
				onClick={() => {
					switch(r_currentTool) {
						case CURRENT_TOOL.PIN:
							NUTOOL_PIN.uncheckAllNodes();
							break;
						case CURRENT_TOOL.CLOCK:
							NUTOOL_CLOCK.uncheckAllNodes();
							break;
						default:
							break;
					}
				}}
			/>
			<img
				src={settings}
				alt="Setting"
				hidden={true} // TODO
				onClick={() => {
					//NUTOOL_PER.settings();
					debugger;
					if (i18n.language === "en"){
						i18n.changeLanguage("zh-tw");
					}
					else if(i18n.language === "zh-tw"){
						i18n.changeLanguage("zh-cn");
					}
					else {
						i18n.changeLanguage("en");
					}
				}}
			/>
			<img
				src={user_manual}
				alt="Manual"
				onClick={ () => {
					// window.open('https://www.nuvoton.com/export/resource-files/UM_NuTool-PinConfigure_EN_Rev1.24.pdf');
					window.open("./UserManual/UM_NuTool-CodeGenerator_EN.pdf");
				}}
			/>
			<img
				id="ID_BUTTON_TEST"
				alt="Manual"
				hidden={true} // TODO
				onClick={ () => {
					// eslint-disable-next-line no-restricted-globals
					external.generateTestResult();
				}}
			/>
		</div>
	);
};

export default Toolbar;