// src/i18n.js
import i18n from 'i18next';
import { initReactI18next } from 'react-i18next';
import en from './i18n/en.json';
import cn from './i18n/zh-CN.json';
import tw from './i18n/zh-TW.json';

const resources = {
  'en': {
    translation: en,
  },
  'zh-CN': {
    translation: cn,
  },
  'zh-TW': {
    translation: tw,
  },
};

i18n.use(initReactI18next).init({
  resources,
  lng: 'en',             //
  fallbackLng: 'en',     //
  interpolation: {
    escapeValue: false,
  },
});

export default i18n;